@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
.text {
  font-size: 1.2rem;
  color: aliceblue;
  font-family: "Lato", sans-serif;
}

.startingColors{
  display: flex;
  justify-content: space-evenly;
  padding: 1rem;
}

.btnBox{
  display: flex;
  justify-content: end;
}

.closeBtn{
background: none;
border: none;
font-size: 1.8rem;
color: aliceblue;
cursor: pointer;
transition: all .3s ease-in-out;

}

.closeBtn:hover{
  transform: scale(1.1);
}





.item{
width: 3rem;
height: 3rem;
border-radius: 50%;
border: 1px solid aliceblue;
}