.Flask {
  display: flex;
  flex-direction: column;
  height: 12rem;
  width: 3rem;
  margin-top: 4rem;
  margin-bottom: 4rem;
  margin-left: .6rem;
  
 
  border-top: none;
  justify-content: flex-end;
  border-radius: 0 0 1rem 1rem;
  overflow: hidden;
  transition: all 0.2s ease-in-out;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0.1),
    rgba(255, 255, 255, 0.4)
  );
}

@media (max-width: 800px) {

  .Flask{
    height: 10rem;
    width: 2.5rem;
  }
 
  
}

.Flask:hover {
  cursor: pointer;
}

.Flask__selected {
  transform: translateY(-1rem);
}

.Flask-item {
  width: 100%;
  height: 24%;
}

.red {
  background-image: linear-gradient(
    to right,
    rgba(255, 69, 0, 1),
    rgba(255, 69, 0, 0.5)
  );
}

.darkRed {
  background-image: linear-gradient(
    to right,
    rgba(173, 9, 9, 1),
    rgba(173, 9, 9, 0.5)
  );
}

