}

.darkGreen {
  background-image: linear-gradient(
    to right,
    rgba(16, 83, 5, 1),
    rgba(16, 83, 5, .5)
  );
}

.yellow {
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 0, 1),
    rgba(255, 255, 0, 0.4)
  );
}

.purple {
  background-image: linear-gradient(
    to right,
    rgba(98, 0, 128,1),
    rgba(98, 0, 128, .6)
  );
}

.pink{
  background-image: linear-gradient(
    to right,
