@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
.text {
  font-size: 1.2rem;
  color: aliceblue;
  transition: all 1.3s ease-in-out;
  font-family: "Lato", sans-serif;
}

.victoryBox{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.victory {
  font-size: 1.8rem;
  color: rgb(215, 230, 12);
  font-family: "Lato", sans-serif;
}

.victorySmall {
  font-size: 1rem;
  color: rgb(215, 230, 12);
  font-family: "Lato", sans-serif;
  margin-top: -1.5rem;
}







.row {
  display: flex;
  justify-content: space-between;

}
.item {
  margin: 1rem; 
  padding: 0 2rem; 
  display: flex;
  font-size: 1.2rem;
  color: aliceblue;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border-radius: 2rem;
}
.item:hover {
  transform: scale(1.05);
  background-color: rgba(216, 13, 172, 0.3);
}


.currentLevel{
  border: 1px solid rgba(216, 13, 172, 0.3);
}

.closeBtn {
  display: flex;
  justify-content: flex-end;
}

.closeIcon {
  color: aliceblue;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.closeIcon:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.3);
}
